.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  background-color: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 100px;
}

#container {
  width: 700px;
  border: 1px solid #e5e5e5;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
  padding: 32px;
}

hr {
  margin: 16px 0;
}

span {
  display: inline-flex;
  align-items: center;
  justify-content: space-around;
}

button {
  display: inline-flex;
  position: relative;
  background-color: green;
  color: white;
  border: 0;
  padding: 8px 16px;
  margin: 16px 16px 16px 8px;
}
